import React, { Component } from "react";
import sound from "./Ding.m4a";
import logo from './logo.jpg';
import './App.css';

class App extends React.Component {
  state = { count: 0, intervalId: 0 , playedAudio: 0, audioId:0, slowTime: 0, fastTime: 0};
  
  
  handleClick = () => {
    if(this.state.intervalId){
      clearInterval(this.state.intervalId);
      
      this.setState(prevState => {
        return {
          ...prevState,
          intervalId: 0,
          count : 0,
          playedAudio:0,
          audioId:0
        };
      });
      return;
    }
    
    const newIntervalId = setInterval(() => {
      
      // Moving from fast to slow
      if(this.state.audioId === 0)
      {
          if(this.state.count > (this.state.fastTime - 2))
          {
            if(this.state.playedAudio === 0)
            {
              new Audio(sound).play();
              this.setState(prevState => {
                return {
                  ...prevState,
                  playedAudio: 1,
                  audioId: prevState.audioId,
                  slowTime: prevState.slowTime,
                  fastTime: prevState.fastTime,
                  count: prevState.count + 1,
                };
              });
            }
            else
            {
              if(this.state.count >= this.state.fastTime)
              {
                this.setState(prevState => {
                  return {
                    ...prevState,
                    playedAudio: 0,
                    audioId: 1,
                    slowTime: prevState.slowTime,
                    fastTime: prevState.fastTime,
                    count: -1,
                  };
                });
              }
              else
              {
                this.setState(prevState => {
                  return {
                    ...prevState,
                    playedAudio: prevState.playedAudio,
                    audioId: prevState.audioId,
                    slowTime: prevState.slowTime,
                    fastTime: prevState.fastTime,
                    count: prevState.count+1,
                  };
                });
              }
              this.setState(prevState => {
                return {
                  ...prevState,
                  playedAudio: prevState.playedAudio,
                  count: prevState.count + 1,
                };
              });
            }
          }
          else{
            this.setState(prevState => {
              return {
                ...prevState,
                playedAudio: prevState.playedAudio,
                audioId: prevState.audioId,
                slowTime: prevState.slowTime,
                fastTime: prevState.fastTime,
                count: prevState.count+1,
              };
            });
          }
      }
      else
      {
        if(this.state.count > (this.state.slowTime - 2))
        {
          if(this.state.playedAudio === 0)
          {
            new Audio(sound).play();
            this.setState(prevState => {
              return {
                ...prevState,
                playedAudio: 1,
                audioId: prevState.audioId,
                slowTime: prevState.slowTime,
                fastTime: prevState.fastTime,
                count: prevState.count + 1,
              };
            });
          }
          else
          {
            if(this.state.count >= this.state.slowTime)
            {
              this.setState(prevState => {
                return {
                  ...prevState,
                  playedAudio: 0,
                  audioId: 0,
                  slowTime: prevState.slowTime,
                  fastTime: prevState.fastTime,
                  count: -1,
                };
              });
            }
            else
            {
              this.setState(prevState => {
                return {
                  ...prevState,
                  playedAudio: prevState.playedAudio,
                  audioId: prevState.audioId,
                  slowTime: prevState.slowTime,
                  fastTime: prevState.fastTime,
                  count: prevState.count+1,
                };
              });
            }
            this.setState(prevState => {
              return {
                ...prevState,
                playedAudio: prevState.playedAudio,
                count: prevState.count + 1,
              };
            });
          }
        }
        else
        {
          this.setState(prevState => {
            return {
              ...prevState,
              playedAudio: prevState.playedAudio,
              audioId: prevState.audioId,
              slowTime: prevState.slowTime,
              fastTime: prevState.fastTime,
              count: prevState.count+1,
            };
          });
        }
      }
    }, 1000);
    
    this.setState(prevState => {
      return {
        ...prevState,
        intervalId: newIntervalId,
      };
    });
  }

  render() {
    return (
      <div>
        <h1>Marathon Mate</h1>
        <h2> Hi There !!! This is a small utility for novice runners to pace themselves. Set the fast and slow interval time inputs in seconds and the page will give you audio hints.</h2>
        <table>
       
          <tr>
            <td width="20%">
              <a>Fast</a>  <input name="FastInput" value={this.state.fastTime} onChange = {event => this.setState(prevState => {
              return {
                ...prevState,
                fastTime:event.target.value
              };
            })}></input>
              <br></br>
              <br></br>
              <a>Slow</a> <input name="SlowInput" value={this.state.slowTime} onChange = {event => this.setState(prevState => {
              return {
                ...prevState,
                slowTime:event.target.value
              };})}></input>
              <br></br>
              <br></br>
              <button onClick={this.handleClick}>
                {this.state.intervalId? "Stop": "Start"}
              </button>
            </td>
            <td>
            <p>{this.state.count}</p>
            </td>
          </tr>
        </table>
        
      </div>
    );
  }
}

export default App;